  const matrixPositions = Object.keys(preset.XML.OverlaySettings).filter(
    overlaySetting => overlaySetting.startsWith('MatrixPosition')
  )

  matrixPositions.forEach(matrixPositionName => {
    const matrixPosition = preset.XML.OverlaySettings[matrixPositionName]
    matrixPosition._text = buildXml(matrixPosition._text)
  })

  const audioBuses = Object.keys(preset.XML).filter(setting => {
    return setting.startsWith('AudioBus') || setting === 'AudioMaster'
  })

  audioBuses.forEach(busName => {
    const bus = preset.XML[busName]
    bus._attributes.AudioRackXML = buildXml(bus._attributes.AudioRackXML)
  })

  // preset.XML.StreamingSettings.StreamingSetting.forEach(streamingSetting => {
