  //         destination._text.xml[`${network}.xml`]._text = buildXml(
  //           wrapXml(destination._text.xml[`${network}.xml`]._text)
  //         )
  //       if (destination._text?.xml?.[`${network}.settingsxml`])
  //         destination._text.xml[`${network}.settingsxml`]._text = buildXml(
  //           wrapXml(destination._text.xml[`${network}.settingsxml`]._text)
  //         )
  //     })
  //   })
  // })

  return buildXml(preset, true)
