const onFileChange = event =>
  new Promise((resolve, reject) => {
    const presetFile = event.target.files[0]

    const reader = new FileReader()
    reader.readAsText(presetFile)

    const fileName = presetFile.name

    reader.onload = () => {
      resolve({ fileName, fileContent: reader.result })
    }

    reader.onerror = () => {
      reject({ fileName, fileContent: reader.error })
    }
  })

const renderTriggers = input => {
  const triggerArray =
    input?._attributes?.Triggers?.ArrayOfInputTrigger?.InputTrigger || []

  return <div className='input-triggers'>Triggers: {triggerArray.length}</div>
}

function getPatchedFilename (filename) {
  const filenameBase = filename.split('.vmix')[0]
  return `${filenameBase}_patched.vmix`
}

const patchTriggers = (preset, startInput) => {
  const inputs = preset.XML.Input

  for (let i = startInput - 1; i < inputs.length - 1; i += 2) {
    const patchedInput = inputs[i]
    const targetInput = inputs[i + 1]

    // const patchedInputId = patchedInput._attributes.Key
    // const patchedInputNumber = i + 1

    const targetInputId = targetInput._attributes.Key
    const targetInputNumber = i + 2

    const overlayInputIn = overlayInput1In(targetInputId, targetInputNumber)
    const overlayInputOff = overlayInput1Off(targetInputId, targetInputNumber)

    // const audioOn = audioInputOn(patchedInputId, patchedInputNumber)
    // const audioOff = audioInputOff(patchedInputId, patchedInputNumber)

    patchedInput._attributes.Triggers.ArrayOfInputTrigger.InputTrigger = [
      overlayInputIn,
      overlayInputOff
    ]

    // targetInput._attributes.Triggers.ArrayOfInputTrigger.InputTrigger = [
    //   audioOn,
    //   audioOff
    // ]
  }

  return preset
}

const downloadPreset = (presetFilename, packedPreset) => {
  const element = document.createElement('a')
  element.setAttribute('href', 'data:text/plain;charset=utf-8,' + packedPreset)
  element.setAttribute('download', getPatchedFilename(presetFilename))

  element.style.display = 'none'
  document.body.appendChild(element)

  element.click()

  document.body.removeChild(element)
}

function App () {
  const [preset, setPreset] = useState(null)
  const [presetFilename, setPresetFilename] = useState(null)
  const [startInput, setStartInput] = useState(1)
  const [cnt, setCnt] = useState(0)
  const [loading, setLoading] = useState(false)

  let inputList = <div>No inputs...</div>

  if (preset) {
    const inputs = preset.XML.Input

    inputList = (
      <div>
        {inputs.map((input, index) => (
          <div className='input' key={input._attributes.Key}>
            <div className='input-number'>{index + 1}</div>
            <div>
              {input._attributes.Title || input._attributes.OriginalTitle}
            </div>

            <br />

            {renderTriggers(input)}
          </div>
        ))}
      </div>
    )
  }

  return (
    <div className='App'>
      {!preset && (
        <div>
          <div>Choose preset file:</div>
          <br />
          <input
            type='file'
            onChange={async event => {
              setLoading(true)
              const { fileName, fileContent } = await onFileChange(event)
              const presetUnpacked = xml2js(fileContent, { compact: true })
              const preset = unpackPreset(presetUnpacked)
              setPreset(preset)
              setPresetFilename(fileName)
              setLoading(false)
            }}
          />
        </div>
      )}

      {loading && (
        <div>
          <br />
          <div>Loading...</div>
        </div>
      )}

      {preset && (
        <div>
          <div>
            <button
              className='button-patch'
              onClick={() => {
                const patchedPreset = patchTriggers(preset, startInput)
                setPreset(patchedPreset)
                setCnt(cnt + 1)
              }}
            >
              Patch triggers
            </button>

            <button
              className='button-download'
              onClick={() => downloadPreset(presetFilename, packPreset(preset))}
            >
              Download preset
            </button>

            <button
              className='button-restart'
              onClick={() => {
                setPreset(null)
                setPresetFilename(null)
                setStartInput(1)
              }}
            >
              Restart
            </button>
          </div>

          <br />

          <div>Inputs: {preset.XML.Input.length}</div>
          <br />

          <label htmlFor='start-from'>Start from input number: </label>
          <input
            type='number'
            id='start-from'
            value={startInput}
            onChange={event => {
              setStartInput(event.target.value)
            }}
          />

          <br />

          <div>{inputList}</div>
        </div>
      )}
    </div>
  )
}

export default App
