const generateTriggerId = () => {
  const suffix = Math.random().toString().slice(2, 14)
  return `50475189-383d-4a8b-a5b3-${suffix}`;
}

export const overlayInput1In = (targetInputId, targetInputNumber) => {
  return {
    DisplayType: {
      _text: 'Default'
    },
    ID: {
      _text: generateTriggerId()
    },
    GetInputByNumber: {
      _text: 'false'
    },
    Disabled: {
      _text: 'false'
    },
    ControllerHidden: {
      _text: 'false'
    },
    Value: {},
    SelectedIndex: {
      _text: '-1'
    },
    ExpandedKey: {
      _text: 'None'
    },
    Key: {
      _text: 'None'
    },
    Function: {
      _text: 'OverlayInput1In'
    },
    Duration: {
      _text: '500'
    },
    Input: {
      Key: {
        _text: String(targetInputId)
      },
      Number: {
        _text: String(targetInputNumber)
      }
    },
    Mix: {
      _text: '0'
    },
    ReplayChannel: {
      _text: 'Current'
    },
    MIDIChannel: {
      _text: '0'
    },
    MIDINote: {
      _text: '0'
    },
    MIDIValue: {
      _text: '0'
    },
    MIDIValueEnabled: {
      _text: 'false'
    },
    MIDIValueType: {
      _text: 'Default'
    },
    JSIndex: {
      _text: '0'
    },
    JSButton: {
      _text: '0'
    },
    JSPressureEnabled: {
      _text: 'false'
    },
    Delay: {
      _text: '0'
    },
    Trigger: {
      _text: 'OnTransitionIn'
    }
  }
}

